@import url(https://fonts.googleapis.com/css2?family=Alkatra:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navouter {
  box-shadow: 0px 10px 8px -6px rgba(0, 0, 0, .5);
}

.navimg {
  margin-left: 10px;
  margin-right: 10px;
  width: 60px;
  height: 55px;
}

.nav-item {
  padding: 0px 5px;
}

.nav-NavLink.active {
  color: rgb(230, 175, 26);
}

.nav-NavLink {
  text-decoration: none;
  color: white;
  padding: 10px 5px;
  border-radius: 6px;
  transition: 0.3s all;
}

.nav-NavLink:hover {
  color: rgb(230, 175, 26);
}

.logoutBtn {
  cursor: pointer;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    /* background: #34485e; */
}

.loginBox {
    width: 350px;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #191919;
    text-align: center;
    border-radius: 10px;
    z-index: 2;
}

.loginBox h1 {
    color: white;
    text-transform: uppercase;
    font-weight: 600;
}
.loginBox input[type="number"]{
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid rgb(230, 175, 26);;
    padding: 14px 10px;
    width: 260px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
}
.errorMessage{
    height: 24px;
}

.loginBox input[type="submit"] {
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    border: 2px solid rgb(230, 175, 26);
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}
.loginBox .loginBtn ,.loginBox .cancelBtn{
    border: 0;
    background: none;
    display: block;
    margin: 20px auto;
    text-align: center;
    padding: 14px 40px;
    outline: none;
    color: white;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;
}
.loginBox .loginBtn{
    border: 2px solid rgb(230, 175, 26);
}
.loginBox .cancelBtn{
    border: 2px solid aqua;
}

.loginBox .loginBtn:hover {
    background: linear-gradient(90deg, #ae7f00, #f0c95f);
    color: black;
}
.loginBox .cancelBtn:hover {
    background: linear-gradient(90deg, #386a6a, aqua);
    color: black;
}
.loginBox input::-webkit-outer-spin-button,
.loginBox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.loginBox input[type=number] {
  -moz-appearance: textfield;
}
