.battingSummary,.bowlingSummary {
    position: relative;
    background: transparent;
    height: 360px;
    width: 360px;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    border-radius: 5px;
    border: 2px solid rgba(0, 0, 0, 0.2);
    transition: 0.5s;
    transition-delay: 0.5s;
}

.battingSummary:before ,.bowlingSummary:before{
    content: '';
    background: rgb(69, 156, 243);
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 0%;
    height: 0%;
    z-index: -1;
    transition: 0.5s;
}

.battingSummary:hover:before ,.bowlingSummary:hover:before{
    background: rgb(69, 156, 243);
    width: 100%;
    height: 100%;
}

.battingSummary:hover ,.bowlingSummary:hover{
    color: white;
    border: 10px solid white;
    box-shadow: 0px 20px 15px rgba(0, 0, 0, 0.2);
}

.battingSummary h4,.bowlingSummary h4{
    font-weight: 600;
    margin: 15px 15px 10px 25px;
}

.battingSummary .bsDetailsLeft ,.bowlingSummary .bsDetailsRight{
    font-size: 16px;
    font-weight: 200;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 5px 25px 25px 25px;
}
